import React, { Component } from 'react';
import { is, fromJS } from 'immutable';
import './Head.scss';
import { NavLink } from 'react-router-dom';
import { Icon } from 'antd'
import PropTypes from 'prop-types';
import logo from '../../assets/image/logo.png'
import bg from '../../assets/image/bg.png'
const _style = {
    backgroundImage: `url(${bg})`
};
export default class Head extends Component{
    static propTypes = {
        isLogin: PropTypes.bool.isRequired,
        isRegister: PropTypes.bool.isRequired
    };
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps))|| !is(fromJS(this.state),fromJS(nextState))
    }
    render(){
        return (
            <div className="login_head" style={_style}>
                <div className="head_wrap">
                    <img src={logo} alt="" className="logo"/>
                    <span>跨港通-通兑平台</span>
                </div>
                {this.props.children ? this.props.children : <div className="head_nav">
                    <NavLink to="/login" className={this.props.isLogin ? "active" : ""}>
                        <span>会员登录</span>
                        <Icon type="caret-up" />
                    </NavLink>
                    <NavLink to="/register" className={this.props.isRegister ? "active" : ""}>
                        <span>会员注册</span>
                        <Icon type="caret-up" />
                    </NavLink>
                </div>
                }
            </div>
        )
    }
}






